let fontUnit = 'upx'

fontUnit = 'rpx'

export default {
	name: 'BjxInputs',
	props: {
		placeholder: {
			type: String,
			default: ''
		},
		value: {
			type: String,
			default: ''
		},
		itemKey: {
			type: String,
			default: ''
		},
		width: {
			type: String,
			default: '600'
		},
		disabled: {
			type: Boolean,
			default: false
		},
		timeDelay: {
			type: Number,
			default: 30
		}
	},
	data() {
		return {
			timer: null,
			data: [],
			isShow: false,
			theValue: this.value,
			theDisabled: this.disabled
		}
	},
	computed: {
		theWidth() {
			return !isNaN(Number(this.width)) ? this.width + fontUnit : this.width
		},
		show(){
			return this.isShow&&this.data.length
		}
	},
	methods: {
		showItem(){
			this.isShow = this.data.length > 0
		},
		theFocus(e){
			this.$emit('focus',e)
		},
		theBlur(e){
			this.$emit('blur',e)
		},
		theInput(e) {
			if(this.timer) {
				clearTimeout(this.timer)
			}
			this.timer = setTimeout(()=>{
				this.$emit('list',{value: e.detail.value,callback: this.setData})
			},this.timeDelay)
		},
		getItem(item) {
			return this.itemKey && typeof item == 'object' ? item[this.itemKey] : item
		},
		selectItem(item) {
			this.isShow = false
			this.theValue = this.itemKey ? item[this.itemKey] : item
			this.$emit('select',item)
		},
		setData(data) {
			if(data) {
				this.isShow = true
				this.data = data
			}
		},
		setValue(value){
			this.theValue = value
		},
		setDisabled(value){
			this.theDisabled = value
		},
	},
}
